import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { TableData, ColEditDelete } from 'components/common';
import { getRatingCertificates, deleteRatingCertificate } from 'services/ratingCertificate';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__CHUNG_CHI__XEP_LOAI_CHUNG_CHI__NHAP_DU_LIEU;
class RatingCertificatePage extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'Xếp hạng',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Tên tiếng anh',
      dataIndex: 'labelInEnglish',
      key: 'labelInEnglish',
    },
    {
      title: 'Từ điểm',
      dataIndex: 'fromGrade',
      key: 'fromGrade',
    },
    {
      title: 'Đến điểm',
      dataIndex: 'toGrade',
      key: 'toGrade',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    try {
      await deleteRatingCertificate(id);
      message.success('Xóa thành công');
      this.setState({ reload: (this.state.reload += 1) });
    } catch (err) {
      message.error('Xóa không thành công');
    }
  };

  render() {
    const { reload } = this.state;
    return (
      <Card title="Xếp loại chứng chỉ">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getRatingCertificates}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RatingCertificatePage />
  </Page>
);



